:root {
  --font-family: 'DM Sans', sans-serif;
  --second-font-family: 'Titan One', sans-serif;
}
.products {
  padding: 60px 16px;
}
.upper-prod {
  display: flex;
  justify-content: center;
  color: #ffa5ba;
  font-family: var(--second-font-family);
  font-weight: 400;
  font-size: 30px;
}

.title-prod {
  display: flex;
  justify-content: center;
  color: #d41443;
  font-family: var(--second-font-family);
  font-weight: 400;
  font-size: 58px;
}

.product-type {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
  justify-items: center;
}

.product-list {
  width: 368px;
  height: 796px;
  padding-left: 16px;
  padding-right: 16px;
}

.text-cont1 {
  background-color: #ffb8ca;
  border-radius: 24px;
  height: 535px;
  width: 368px;
  color: white;
  align-content: center;
  position: relative;
  bottom: 130px;
}

.text-cont2 {
  background-color: #f0d1a5;
  border-radius: 24px;
  height: 535px;
  width: 368px;
  color: white;
  align-content: center;
  position: relative;
  bottom: 130px;
}

.text-cont3 {
  background-color: #c2e297;
  border-radius: 24px;
  height: 535px;
  width: 368px;
  color: white;
  align-content: center;
  position: relative;
  bottom: 130px;
}

.just-text {
  display: grid;
  justify-items: center;
  top: 32px;
  text-align: center;
}

.prod-1 {
  margin-top: 32px;
  font-size: 30px;
  font-family: var(--second-font-family);
  font-weight: 400;
}

.prod-1::after {
  content: '...';
  position: absolute;
  color: white;
  display: flex;
  left: 5.7em;
}

.product-pic {
  position: relative;
  z-index: 1;
}

.prod-description {
  width: 304px;
  font-size: 16px;
  font-weight: 700;
  font-family: var(--font-family);
}

.arrow {
  width: 15px;
  height: 15px;
}

.modal-svg-btn {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 0;
}

.modal-svg-btn:hover {
  border: 2px solid rgb(212, 20, 67);
}

/*aici css modal*/
.modal-more-info {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-MI-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
