:root {
  --font-family: "DM Sans", sans-serif;
  --second-font-family: "Titan One", sans-serif;
  --about-label: #ffa5ba;
  --about-title: #d41443;
  --about-bg-color: #fff5f6;
  --about-p-color: #000;
  --about-span-color: #907e82;
  --about-btn-hover-color: #ecebeb;
}

.about-container {
  width: 100%;
  height: 780px;
  background-color: var(--about-bg-color);
}

.about-content {
  background-image: url("about-background.73763630.png");
  background-repeat: no-repeat;
  background-size: 100% 521px;
  margin-top: 50px;
  display: flex;
}

.about-headings {
  width: 534px;
  height: 116px;
  border: 400px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 60px;
}

.about-label {
  width: 351px;
  height: 34px;
  font-family: var(--second-font-family);
  color: var(--about-label);
  letter-spacing: 3px;
  margin-left: 90px;
  font-size: 27px;
  font-weight: 400;
}

.about-title {
  width: 534px;
  height: 66px;
  font-family: var(--second-font-family);
  color: var(--about-title);
  letter-spacing: 6px;
  font-size: 53px;
  font-weight: 400;
}

.about-p {
  font-family: var(--font-family);
  color: var(--about-p-color);
  margin-bottom: 32px;
  padding-top: 16px;
  font-size: 16px;
  font-weight: 500;
}

.about-span {
  font-family: var(--font-family);
  color: var(--about-span-color);
  font-size: 14px;
  font-weight: 500;
}

.about-btn {
  width: 176px;
  height: 44px;
  color: var(--about-title);
  font-family: var(--font-family);
  background-color: #fff;
  border: rgba(0, 0, 0, 0);
  border-radius: 22px;
  align-content: center;
  align-items: center;
  margin-top: 40px;
  font-size: 16px;
  font-weight: 700;
}

.about-btn:hover {
  background-color: var(--about-btn-hover-color);
}

.cow {
  width: 501px;
  height: 501px;
  margin-top: 60px;
  margin-left: 16px;
}

.about-text {
  margin-top: 9%;
  margin-left: 15%;
}

.is-hidden {
  visibility: hidden;
}

.about {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  justify-content: center;
  margin-top: 6%;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  bottom: 10px;
  left: 0;
  right: 0;
}

.modal-about-content {
  background-color: var(--about-bg-color);
  width: 50%;
  height: 500px;
  padding: 16px;
  position: relative;
}

.about-close-btn {
  background-color: var(--about-bg-color);
  border: rgba(0, 0, 0, 0);
  outline: none;
  position: absolute;
  top: 15px;
  right: 15px;
}

.x-btn {
  color: #000;
}

.modal-about-title {
  font-family: var(--second-font-family);
  color: var(--about-title);
  margin: 16px;
}

.about-modal-text {
  color: var(--about-span-color);
  font-family: var(--second-font-family);
  letter-spacing: 1px;
}

.cow-modal {
  width: 300px;
  height: 300px;
  margin-bottom: 5%;
}

@media screen and (max-width: 768px) {
  .about-content {
    width: 100%;
    height: 1050px;
    flex-direction: column;
    margin-bottom: 50px;
    display: flex;
  }

  .about-container {
    height: 850px;
  }

  .cow {
    width: 280px;
    height: 279px;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .about-text {
    margin-top: -30px;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .about-headings {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .about-title {
    text-align: center;
    width: 70%;
    justify-content: center;
    margin-left: 80px;
    font-size: 35px;
    display: flex;
  }

  .cow-modal {
    width: 150px;
    height: 150px;
  }

  .about-label {
    margin-left: 135px;
    font-size: 18px;
  }
}

@media screen and (min-width: 769px) and (max-width: 1199px) {
  .about-text {
    margin-left: 6%;
  }

  .about-content {
    background: none;
    margin-top: 80px;
  }
}

:root {
  --font-family: "DM Sans", sans-serif;
  --second-font-family: "Titan One", sans-serif;
  --advantages-bg-color: #fff5f6;
  --advantages-title-color: #d41443;
  --advantages-paragraph-text-color: #000;
}

ul {
  list-style-type: none;
}

.advantages {
  background-color: var(--advantages-bg-color);
  padding-bottom: 180px;
}

.title-hidden {
  display: none;
}

.advantages ul {
  flex-direction: column;
  justify-content: flex-start;
  gap: 22px;
  margin: 0;
  display: flex;
}

.advantages h2 {
  font-family: var(--second-font-family);
  letter-spacing: .04em;
  color: var(--advantages-title-color);
  font-size: 58px;
  font-weight: 400;
  line-height: 66%;
}

.advantages p {
  font-family: var(--font-family);
  letter-spacing: .02em;
  color: var(--advantages-paragraph-text-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
}

.advantages .icon {
  width: 50px;
  height: 50px;
}

@media screen and (min-width: 768px) {
  .advantages ul {
    flex-direction: row;
    justify-content: flex-start;
    gap: 20px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .advantages ul {
    flex-direction: row;
    justify-content: flex-start;
    gap: 80px;
    display: flex;
  }
}

:root {
  --font-family: "DM Sans", sans-serif;
  --second-font-family: "Titan One", sans-serif;
  --header-btn-bg-color: #fff;
  --header-btn-active-bg-color: #ecebeb;
  --header-btn-text: #d41443;
  --header-text-color: #fff;
  --header-text-color-hover: #d41443;
  --hero-bg-color: #ffb8ca;
  --hero-ice-cream-bg-color: #ffa5ba;
  --hero-title-text-color: #fff;
  --hero-title-color-red: #d41443;
  --hero-text-color: #fff;
  --hero-arrow-btn: #fff;
  --hero-products-btn-bg-color: #d41443;
  --hero-him-btn-bg-color: #fff;
  --hero-button-active-bg-color: #ecebeb;
  --hero-products-button-text: #fff;
  --hero-him-button-text: #d41443;
  --hero-number-color-text: #d41443;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --navy-blue-modal: rgba(46, 47, 66, .4);
  --modal-background: #fff5f6;
  --modal-close-btn: #2e2f42;
  --modal-cole-btn-bg: #e7e9fc;
  --form-heading-color: #2e2f42;
  --form-label-color: #8e8f99;
  --form-icon-color: #2e2f42;
  --checkbox-color: #ffa5ba;
  --checkbox-text-color: #8e8f99;
  --checkbox-policy-txt-color: #ffa5ba;
}

html {
  scroll-behavior: smooth;
}

ul {
  list-style-type: none;
}

a {
  transition: background-color, color var(--transition);
  text-decoration: none;
}

.global-header {
  background-color: var(--hero-bg-color);
  width: 100%;
  margin: 0, auto;
}

.header {
  z-index: 3;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 16px;
  padding-top: 50px;
  display: flex;
}

.header ul {
  gap: 35px;
  padding: 0;
  display: flex;
  overflow: visible;
}

.header-logo svg {
  width: 199px;
  height: 50px;
}

.burger-menu-btn {
  height: 40px;
  width: 40px;
  color: #d41443;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  top: auto;
  right: 20px;
}

.burger-menu-btn:hover, .burger-menu-btn:focus {
  background-color: rgba(0, 0, 0, .1);
}

.buy-now-btn {
  width: 159px;
  height: 44px;
  background-color: var(--header-btn-bg-color);
  color: var(--header-btn-text);
  font-family: var(--font-family);
  text-align: center;
  cursor: pointer;
  border-width: 0;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
}

.buy-now-btn:hover {
  background-color: var(--header-btn-active-bg-color);
  transition: background-color var(--transition);
}

.header-btn-text {
  font-family: var(--font-family);
  text-align: center;
  color: var(--header-btn-text);
  font-size: 16px;
  font-weight: 700;
}

.nav-bar {
  font-family: var(--font-family);
  z-index: 3;
  font-size: 16px;
  font-weight: 700;
}

.nav-bar a, .nav-bar a:visited {
  color: var(--header-text-color);
}

.nav-bar a:hover {
  color: var(--header-text-color-hover);
}

.hero-left {
  z-index: 3;
}

.ice-cream-made {
  font-family: var(--second-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--hero-title-text-color);
  text-shadow: 0 4px 4px rgba(0, 0, 0, .25);
  padding-top: 50px;
  font-size: 38px;
  font-weight: 400;
  line-height: 90%;
}

.passion {
  font-family: var(--second-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--hero-title-color-red);
  text-shadow: 0 4px 4px rgba(0, 0, 0, .25);
  font-size: 58px;
  font-weight: 400;
  line-height: 90%;
}

.hero-buttons ul {
  border-width: 0;
  flex-direction: row;
  gap: 12px;
  padding-left: 0;
  display: flex;
}

.hero-products-btn {
  width: 107px;
  height: 40px;
  background-color: var(--hero-products-btn-bg-color);
  color: var(--hero-products-button-text);
  font-family: var(--font-family);
  text-align: center;
  cursor: pointer;
  border-width: 0;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 400;
}

.hero-howitsmade-btn {
  width: 138px;
  height: 40px;
  background-color: var(--hero-him-btn-bg-color);
  color: var(--hero-him-button-text);
  font-family: var(--font-family);
  text-align: center;
  cursor: pointer;
  border-width: 0;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 400;
}

.hero-howitsmade-btn:hover {
  background-color: var(--hero-button-active-bg-color);
  transition: background-color var(--transition);
}

.hero-left-text {
  font-family: var(--font-family);
  color: var(--hero-text-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
}

.hero-arrow-btn {
  width: 26px;
  height: 26px;
  background-color: var(--hero-arrow-btn);
  cursor: pointer;
  border-width: 0;
  border-radius: 50%;
  justify-content: center;
  display: flex;
}

.hero-arrow-btn svg {
  width: 26px;
  height: 26px;
  fill: #d41443;
}

.cafes {
  flex-direction: column;
  padding-bottom: 25px;
  display: flex;
  position: relative;
}

.trucks {
  flex-direction: column;
  display: flex;
  position: relative;
}

.hero-number {
  font-family: var(--second-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  text-align: right;
  color: var(--hero-number-color-text);
  z-index: 9;
  font-size: 46px;
  font-weight: 400;
}

.hero-elipse {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: -10px;
  right: -10px;
}

.hero-cafes-trucks-text {
  font-family: var(--font-family);
  text-align: right;
  color: var(--hero-text-color);
  font-size: 14px;
  font-weight: 400;
}

h1 span {
  display: flex;
}

.hero {
  height: 664px;
  padding-left: 16px;
  display: flex;
  position: relative;
}

.hero-milk-pitcher {
  flex: column;
  align-items: center;
  padding-top: 40px;
  display: flex;
}

.hero-milk-pitcher img {
  animation: pitcher 1s linear infinite;
}

@keyframes pitcher {
  100% {
    transform: translateY(-2px);
  }

  50% {
    transform: translateY(2px);
  }

  0% {
    transform: translateY(-2px);
  }
}

.hero-right {
  display: flex;
}

.hero-tasty {
  z-index: 4;
  position: absolute;
  bottom: 0;
  right: 16px;
}

.hero-ice-cream {
  z-index: 2;
  position: absolute;
  bottom: 0;
  right: 328px;
}

.hero-pink-elipse {
  z-index: 1;
  position: absolute;
  bottom: 160px;
  right: 170px;
}

.hero-cafes-trucks {
  flex-direction: column;
  gap: 25px;
  display: flex;
  position: absolute;
  top: 68px;
  right: 16px;
}

.is-hidden {
  visibility: hidden;
  scale: 1.05;
}

.modal {
  width: 100vw;
  height: 100vh;
  z-index: 99999;
  background: var(--navy-blue-modal);
  transition: transform var(--transition);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: var(--modal-background);
  width: 408px;
  height: 584px;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  display: flex;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 1px 2px rgba(0, 0, 0, .2);
}

.modal-close-btn {
  width: 24px;
  height: 24px;
  color: #d41443;
  cursor: pointer;
  background-color: #ffa5ba;
  border-style: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.modal-heading {
  letter-spacing: .02em;
  text-align: center;
  color: var(--header-text-color-hover);
  margin-top: 40px;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
}

.form-group {
  flex-direction: column;
  gap: 4px;
  margin-bottom: 8px;
  display: flex;
  position: relative;
}

.form-input:focus {
  border-color: #ffa5ba;
  outline: none;
}

.form-input:focus + .form-icon {
  color: #ffa5ba;
}

.form-icon {
  width: 18px;
  height: 24px;
  color: var(--form-icon-color);
  color: var(--header-btn-text);
  padding: 3px 0;
  position: absolute;
  top: 50%;
  left: 16px;
}

.form-input {
  width: 360px;
  height: 40px;
  resize: none;
  border: 1px solid rgba(46, 47, 66, .4);
  border-radius: 4px;
}

.form-group .form-input {
  padding-left: 2.5rem;
  left: .5rem;
}

.form-label {
  letter-spacing: .04em;
  color: var(--form-label-color);
  font-size: 12px;
  line-height: 1.16667;
}

.form-textarea {
  letter-spacing: .04em;
  color: rgba(46, 47, 66, .4);
  width: 360px;
  height: 120px;
  resize: none;
  border: 1px solid rgba(46, 47, 66, .4);
  border-radius: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
}

.form-textarea:focus {
  border-color: #ffa5ba;
  outline: none;
}

.agree-check {
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

.agree-check .checkmark {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

.agree-check .check-icon {
  width: 16px;
  height: 16px;
  fill: none;
  cursor: pointer;
  border: 1px solid rgba(46, 47, 66, .4);
  border-radius: 2px;
  margin-right: 8px;
}

.agree-check .checkmark:focus + .check-icon, .agree-check .checkmark:checked:focus + .check-icon {
  outline: 1px solid rgba(46, 47, 66, .4);
}

.agree-check .checkmark:checked + .check-icon {
  background-color: var(--checkbox-color);
  border-color: var(--checkbox-color);
  fill: var(--checkbox-color);
  stroke: var(--checkbox-color);
  stroke-width: .2px;
  outline: none;
}

.check-text {
  letter-spacing: .04em;
  color: var(--checkbox-text-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
}

.check-policy {
  -webkit-text-decoration-skip-ink: none;
  text-decoration-skip-ink: none;
  color: var(--checkbox-policy-txt-color);
  line-height: 1.33333;
  text-decoration: underline;
}

.modal-send-btn {
  width: 169px;
  height: 56px;
  background: var(--header-btn-bg-color);
  letter-spacing: .04em;
  text-align: center;
  color: var(--header-text-color-hover);
  cursor: pointer;
  border-style: none;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-send-btn:hover {
  background-color: var(--header-btn-active-bg-color);
  transition: background-color var(--transition);
  transform: scale(1.05);
}

@media screen and (max-width: 768px) {
  .nav-bar, .buy-now-btn, .hero-cafes-trucks, .hero-tasty, .hero-milk-pitcher {
    display: none;
  }

  .ice-cream-made {
    font-size: 26px;
  }

  .passion {
    font-size: 46px;
  }

  .hero-products-btn {
    width: 62px;
    height: 24px;
    font-size: 8px;
  }

  .hero-howitsmade-btn {
    width: 82px;
    height: 24px;
    font-size: 8px;
  }

  .hero-ice-cream {
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .hero-pink-elipse {
    position: absolute;
    bottom: 75px;
    right: 0;
  }

  .header-logo, .burger-menu-btn {
    z-index: 4;
  }

  .hero-left {
    margin-top: 140px;
    margin-left: 30px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .burger-menu-btn {
    position: absolute;
    top: auto;
    right: 200px;
  }

  .nav-bar {
    display: none;
  }

  .ice-cream-made {
    font-size: 22px;
  }

  .passion {
    font-size: 42px;
  }

  .hero-number {
    font-size: 28px;
  }

  .hero-cafes-trucks-text {
    font-size: 8px;
  }

  .hero-elipse {
    width: 16px;
    height: 16px;
    position: absolute;
    top: -5px;
    right: -5px;
  }

  .hero-products-btn {
    width: 62px;
    height: 24px;
    font-size: 8px;
  }

  .hero-howitsmade-btn {
    width: 82px;
    height: 24px;
    font-size: 8px;
  }

  .hero-left-text {
    font-size: 8px;
    line-height: 1.5;
  }

  .global-header {
    height: 664px;
  }

  .hero-tasty {
    position: absolute;
    bottom: 50px;
    right: 32px;
  }

  .hero-ice-cream {
    position: absolute;
    bottom: 100px;
    right: 303px;
  }

  .header {
    z-index: 10;
  }
}

@media screen and (min-width: 1280px) {
  .menu-for-mobile, .burger-menu-btn {
    display: none;
  }
}

:root {
  --font-family: "DM Sans", sans-serif;
  --second-font-family: "Titan One", sans-serif;
  --footer-background-color: #fff5f6;
  --white-color: #fff;
  --btn-footer-color: #d41443;
  --btn-footer-hover-background: #d41342;
  --homemade-color-footer: #ffa5ba;
  --background-footer-icon: #fea5bb;
  --passion-color-footer: #d41443;
  --contact-phone-color: #d41443;
  --contact-text-color: #000;
  --copyright-footer-color: #907e82;
  --line-footer-color: #e1e1e1;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.global-footer {
  background-color: var(--footer-background-color);
  height: 877px;
  flex-direction: column;
  justify-content: flex-end;
  display: flex;
  position: relative;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.homemade-text {
  font-family: var(--second-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--homemade-color-footer);
  width: 232px;
  flex-wrap: wrap;
  font-size: 18px;
  font-weight: 400;
  display: flex;
}

.passion-text {
  font-family: var(--second-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--passion-color-footer);
  width: 200px;
  padding-top: 15px;
  font-size: 34px;
